@import url('https://fonts.googleapis.com/css2?family=Anton&family=Antonio:wght@200;400&display=swap');
@import 'reset';
@import 'mixin'; 
@import 'mob';


$header_color:#ed6015;
header{
    background:$header_color;
    color:white;
}
.fa-bars{
    display: none;
    
}
nav{
   @include flexBox(space-between); 
    width:100%;
    position: relative;
}
.menu{
    @include flexBox(center);
    width:600px;
    margin-bottom: 0;
    padding:0;
    li{
        padding:18px 25px;
        font-size: 16px;
//        background: green;
//        height: 100%;
        &:hover{
            border-bottom: 3px solid #eddcdc;
            background: #585353
        }
    }
}

.icons{
    @include flexBox(space-around);
    width:150px;
    font-size: 23px;
    cursor: pointer;
}

main{
    display:relative;
    width:100%;
    height: 100%;
}
.overlay{
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    background: rgba(0, 0, 0, 0.47);
    z-index: 2;
    height: 100%;
    width:100%;
}
.splide__list{
    width:100%;
    height:500px;
}
.splide__list img{
    width:100%;
    height:100%;
    object-fit: cover;
    filter: grayscale(100%);
    &::after{
        
    }
    
}
.splide__arrows
{
 display: none;
}

.contents{
    position: absolute;
    top:330px;
    left:50%;
    z-index:9;
    @include flexBox(center);
    flex-flow: column;
    transform: translate(-50%,-50%);
    color: white;
    text-align: center;
//    height:100%;
    width:100%;
    @include mq(1100px,1400px){
        top:350px;
    }
    h2{
        letter-spacing: 2px;
        margin: 20px 5px;
        font-size: 49px;
        font-weight: 120%;
        font-family: 'Antonio', sans-serif;
    }
    h1{
        letter-spacing: 3px;
        text-transform: uppercase;
        font-size: 60px;
        font-weight: 100;
        font-family: 'Anton', sans-serif;
        
    }
    .warning{
        background:transparent;
        color: #fff;
        padding:25px 30px;
        margin:25px;
        border:2px solid #fff;
        outline: none;
        font-size: 20px;
        font-weight: bold;
        text-transform: uppercase;
        &:focus,
        &:hover{
            background: $header_color;
            color:#501e0f;
            border:none;
        }
    }
}


.worker-area{
    h2{
    font-weight: 700;
    }
}
/*---------------------------Mobile-------------------------------*/

@media(max-width:600px){
    nav{
        padding:12px 0px;
    }
    .fa-bars{
        display: block;
        font-size: 33px;
    }
    .menu{
        display: block;
        position: absolute;
        top:0px;
        left:-17px;
        bottom: 0;
        width:200px;
        background: rgba(255, 255, 255, 0.65);
        backdrop-filter:blur(10px);
        
        transition: all 0.3s ease-in;
        z-index:7;
       padding-top:20px;
//        height:900px;
//        height: 100%;
        li{
            text-align: center;
            letter-spacing: 1.4px;
            font-family: sans-serif;
            &:hover{
                background: $header_color !important;
                color:#501e0f;
            }
        }
    }
    
    .icons{
        width:130px;
        font-size: 24px;
    }
    .backdrop{
        position: fixed;
        background: rgba(0, 0, 0, 0.9);
        top:45px;
        left:0;
        right: 0;
        bottom: 0;
        z-index: 4;
      transform: translateX(-100%);
        transition: all 0.3s ease-in;
    }
    .splide__list{
        height:92vh;
    }
    .contents{
        top:50%;
        z-index: 3;
        h2{
           font-size: 30px; 
        }
        h1{
           font-size: 50px;  
        }
    }
}

.menu-show{
    transform: translateX(0);
}
.d-show{
    opacity: 1;
}


/*---------------------------Tab-------------------------------*/

@include mq(600px,1100px){
    .menu{
        width:100%;
        li{
            padding:10px 13px;
        }
    }
    .contents{
    top:325px;
    }
}

/*---------------------------pc-------------------------------*/

@include mq(1100px,1400px){
    .contents{
        top:350px;
    }
}



